#splash {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: #fff;
  text-align: center;
}
#splash_logo {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  width: 100px;
  height: 100px;
}

.splash_animation {
  animation: splash_animation 2s infinite;
}

@keyframes splash_animation {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.header {
  display: flex;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 74px;
  padding: 0 20px;
  z-index: 10;
}
.header_logo {
  height: 40px;
  z-index: 100;
}
.header_logo_link {
  display: flex;
}
.header_logo_img, .header_logo_img_scrolled {
  width: 40px;
  height: 40px;
  margin-right: 15px;
}
.header_logo_img_scrolled {
  display: none;
}
.header_logo_text {
  font-size: 32px;
  color: #fff;
}
.header_nav_pc {
  margin-left: 120px;
  color: #fff;
  z-index: 100;
}
.header_nav_pc_list {
  display: flex;
}
.header_nav_pc_item {
  position: relative;
  margin-right: 33px;
}
.header_nav_pc_item::after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #fff;
  content: "";
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.6s ease;
}
.header_nav_pc_item:hover::after {
  transform: scale(1);
}
@media (max-width: 768px) {
  .header_nav_pc {
    display: none;
  }
}
.header_nav_sp {
  display: none;
  z-index: 100;
}
.header_nav_sp_checkbox {
  display: none;
}
.header_nav_sp_checkbox:checked ~ label span {
  background: transparent;
}
.header_nav_sp_checkbox:checked ~ label span:before {
  transform: rotate(45deg);
  top: 0;
}
.header_nav_sp_checkbox:checked ~ label span:after {
  transform: rotate(-45deg);
  top: 0;
}
.header_nav_sp_checkbox:checked ~ nav {
  opacity: 1;
  transform: translateY(0);
}
.header_nav_sp_toggle {
  position: relative;
  cursor: pointer;
  display: block;
  width: 30px;
  height: 30px;
  margin-left: 20px;
  z-index: 10;
}
.header_nav_sp_toggle_line, .header_nav_sp_toggle_line:before, .header_nav_sp_toggle_line:after {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: block;
  width: 30px;
  height: 3px;
  margin: auto;
  background: #fff;
  transition: all 0.3s ease;
}
.header_nav_sp_toggle_line:before, .header_nav_sp_toggle_line:after {
  content: "";
}
.header_nav_sp_toggle_line:before {
  top: 14px;
}
.header_nav_sp_toggle_line:after {
  top: -20px;
}
.header_nav_sp_menu {
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(40, 47, 53, 0.8);
  overflow: hidden;
  transform: translateY(-100%);
  transition: transform 0.6s ease, opacity 1s ease;
}
.header_nav_sp_menu ul {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.header_nav_sp_menu ul li {
  font-size: 3rem;
  color: #fff;
}
.header_nav_sp_menu ul li:not(:last-child) {
  margin-bottom: 1.3em;
}
.header_nav_sp_menu_logo {
  text-align: center;
}
.header_nav_sp_menu_logo img {
  width: 40px;
  height: 40px;
  margin-bottom: 20px;
}
.header_nav_sp_menu_logo span {
  display: block;
  font-size: 3.5rem;
  letter-spacing: 1.3rem;
}
@media (max-width: 768px) {
  .header_nav_sp {
    display: block;
  }
}
.header_reserve {
  margin-left: auto;
  display: flex;
  align-items: center;
  height: 49px;
  padding: 0 32px;
  background: #978f10;
  color: #fff;
  transition: background-color 0.3s ease;
  cursor: pointer;
  z-index: 100;
}
.header_reserve img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.header_reserve:hover {
  background: #c5bb15;
}
@media (max-width: 576px) {
  .header_logo_img {
    width: 35px;
    height: 35px;
    margin-right: 8px;
  }
  .header_logo_text {
    font-size: 2.8rem;
  }
  .header_reserve {
    padding: 0 10px;
  }
  .header_reserve img {
    width: 20px;
    height: 20px;
    margin-right: 5px;
  }
}

.headerBgChange {
  background: rgba(255, 255, 255, 0.8);
  position: fixed;
  opacity: 0;
  transform: translateY(-1);
}
.headerBgChange .header_logo_img {
  display: none;
}
.headerBgChange .header_logo_img_scrolled {
  display: inline;
}
.headerBgChange .header_logo_text,
.headerBgChange .header_nav_pc {
  color: #000;
}
.headerBgChange .header_nav_sp_toggle_line {
  background: #000;
}
.headerBgChange .header_nav_sp_toggle_line::before, .headerBgChange .header_nav_sp_toggle_line::after {
  background: #000;
}

.headerScrollOpen {
  animation: header_down_animation 1s ease forwards;
}

.headerScrollClose {
  animation: header_up_animation 1s ease;
}

@keyframes header_down_animation {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes header_up_animation {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}
.mv_top {
  height: 100vh;
  position: relative;
  overflow: hidden;
}
.mv_top_img_wrapper {
  position: relative;
