  height: 100%;
}
.mv_top_img_item {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: scale 10s ease;
}
.mv_top_img_item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}
.mv_top_img1 {
