  justify-content: center;
}
.info_tab_item {
  position: relative;
  cursor: pointer;
}
.info_tab_item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #000;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.6s ease;
}
.info_tab_item:hover::after {
