.totop {
  position: fixed;
  inset: auto 40px 40px auto;
  display: none;
  width: 70px;
  height: 70px;
  background: #978f10;
  transition: background-color 0.6s ease;
}
.totop::before, .totop::after {
  content: "";
  position: absolute;
  top: 20px;
  height: 30px;
  width: 3px;
  background: #fff;
}
.totop::before {
  rotate: 45deg;
  left: 24px;
}
.totop::after {
  rotate: -45deg;
  right: 24px;
}
.totop:hover {
  background: #c5bb15;
}
@media (max-width: 768px) {
  .totop {
    bottom: 20px;
    right: 20px;
  }
}

.reserve_modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 999;
}
.reserve_modal_overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(40, 47, 53, 0.8);
}
.reserve_modal_body {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  width: 350px;
  padding: 30px 0;
  border-radius: 10px;
  background: #f6f6f6;
  text-align: center;
}
.reserve_modal_icon {
  position: absolute;
  top: 10px;
  right: 16px;
  width: 16px;
  height: 16px;
  cursor: pointer;
}
.reserve_modal_form {
  display: block;
  padding: 10px 30px 0;
  text-align: left;
}
.reserve_modal_title {
  font-size: 3.2rem;
  letter-spacing: 1rem;
}
.reserve_modal label {
  display: block;
  margin-bottom: 5px;
}
.reserve_modal input,
.reserve_modal select {
  display: block;
  width: 100%;
  margin-bottom: 20px;
  padding: 5px;
  border: 1px solid #e7e7e7;
  border-radius: 3px;
}
.reserve_modal input::placeholder,
.reserve_modal select::placeholder {
  color: #bbb;
}
.reserve_modal input:focus,
.reserve_modal select:focus {
  border: 3px solid #e7e7e7;
  margin-bottom: 16px;
}
.reserve_modal button {
  display: block;
  width: 150px;
  margin: 0 auto;
  padding: 10px;
  background: #978f10;
  color: #fff;
  font-size: 2rem;
  letter-spacing: 2rem;
  text-indent: 2rem;
  text-align: center;
}

.stopScroll {
